"empty-results\">\r\n          <el-icon size=\"64\"><DocumentRemove /></el-icon>\r\n          <p>Êú™ÊâæÂà∞Áõ∏ÂÖ≥ÊñáÁ´†</p>\r\n        </div>\r\n      </div>\r\n      \r\n      <div v-else class=\"search-placeholder\">\r\n        <el-icon size=\"100\"><Search /></el-icon>\r\n        <p>ËæìÂÖ•ÂÖ≥ÈîÆËØçÂºÄÂßãÊêúÁ¥¢</p>\r\n      </div>\r\n      \r\n      <!-- XSS ÊèêÁ§∫Ôºà‰ªÖÂú® VULN Ê®°ÂºèÂ±ïÁ§∫Ôºâ -->\r\n      <div class=\"demo-info card\" v-if=\"configStore.xssMode === 'vuln'\">\r\n        <h3>‚ö†Ô∏è XSS ÊºîÁ§∫ÊèêÁ§∫ÔºàÂú∫ÊôØ 1/2 ÂèçÂ∞ÑÂûã XSSÔºâ</h3>\r\n        <p>ÂΩìÂâçÂ§Ñ‰∫é VULN Ê®°ÂºèÔºåÊêúÁ¥¢Ê°ÜÂ≠òÂú®ÂèçÂ∞ÑÂûã XSS ÊºèÊ¥û„ÄÇ</p>\r\n        \r\n        <p style=\"margin-top: 12px;\"><strong>Âú∫ÊôØ 1 - Âü∫Á°ÄÂºπÁ™óÈ™åËØÅÔºö</strong></p>\r\n        <code>&lt;img src=x onerror=alert(1)&gt;</code>\r\n        \r\n        <p style=\"margin-top: 12px;\"><strong>Âú∫ÊôØ 2 - ÈùôÈªòÁîªÂÉèÊî∂ÈõÜÔºö</strong></p>\r\n        <code>ËßÅÊñáÊ°£ÂÆåÊï¥ PayloadÔºàÂê´Êî∂ÈõÜÂô®‰∏äÊä•ÈÄªËæëÔºâ</code>\r\n        \r\n        <p style=\"margin-top: 12px; font-size: 0.9em; color: var(--color-text-muted);\">\r\n          üí° Ê≥®ÊÑèÔºöVue ‰∏≠ÈÄöËøá v-html ÊèíÂÖ•ÁöÑ &lt;script&gt; Ê†áÁ≠æ‰∏ç‰ºöÊâßË°åÔºåÈúÄ‰ΩøÁî®‰∫ã‰ª∂Â§ÑÁêÜÂô®Âûã payloadÔºàÂ¶Ç onerror„ÄÅonload Á≠âÔºâ\r\n        </p>\r\n      </div>\r\n    </div>\r\n  </div>\r\n</template>\r\n\r\n<script setup>\r\n// ÊêúÁ¥¢È°µÔºàÂú∫ÊôØ 1/2ÔºâÔºöÊºîÁ§∫ÂèçÂ∞ÑÂûã XSS ‰∏éÈùôÈªòÊï∞ÊçÆÊî∂ÈõÜ\r\nimport { ref, onMounted } from 'vue';\r\nimport { useRoute, useRouter } from 'vue-router';\r\nimport { useConfigStore } from '@/stores/config';\r\nimport axios from '@/api/axios';\r\nimport ArticleCard from '@/components/ArticleCard.vue';\r\nimport { Search, DocumentRemove } from '@element-plus/icons-vue';\r\n\r\nconst route = useRoute();\r\nconst router = useRouter();\r\nconst configStore = useConfigStore();\r\n\r\nconst query = ref('');\r\nconst searched = ref(false);\r\nconst results = ref([]);\r\nconst resultMessage = ref('');\r\n\r\nconst handleSearch = async () => {\r\n  if (!query.value.trim()) return;\r\n  \r\n  router.push({ path: '/search', query: { q: query.value } });\r\n  \r\n  try {\r\n    const response = await axios.get('/search', { params: { q: query.value } });\r\n    \r\n    // ‰∏çÂêåÊ®°Âºè‰∏ãÔºåÂêéÁ´ØËøîÂõûÁöÑÊèêÁ§∫ÊñáÊ°à‰∏çÂêåÔºàVULN Êú™ËΩ¨‰πâ / SECURE Â∑≤ËΩ¨‰πâÔºâ\r\n    resultMessage.value = response.data.message;\r\n    results.value = response.data.items || [];\r\n    searched.value = true;\r\n  } catch (error) {\r\n    console.error('Search failed:', error);\r\n  }\r\n};\r\n\r\nonMounted(() => {\r\n  if (route.query.q) {\r\n    query.value = route.query.q;\r\n    handleSearch();\r\n  }\r\n});\r\n</script>\r\n\r\n<style scoped>\r\n.search-page {\r\n  padding: var(--spacing-2xl) 0;\r\n  min-height: calc(100vh - 80px);\r\n}\r\n\r\n.search-header {\r\n  margin-bottom: var(--spacing-2xl);\r\n}\r\n\r\n.search-header h1 {\r\n  font-size: 2.5rem;\r\n  margin-bottom: var(--spacing-lg);\r\n  text-align: center;\r\n  background: linear-gradient(135deg, var(--color-primary), var(--color-accent));\r\n  -webkit-background-clip: text;\r\n  -webkit-text-fill-color: transparent;\r\n  background-clip: text;\r\n}\r\n\r\n.result-message {\r\n  font-size: 1.1rem;\r\n  color: var(--color-text-secondary);\r\n  margin-bottom: var(--spacing-xl);\r\n  padding: var(--spacing-md);\r\n  background: var(--color-bg-card);\r\n  border-radius: var(--radius-sm);\r\n  border-left: 4px solid var(--color-primary);\r\n}\r\n\r\n.results-grid {\r\n  display: grid;\r\n  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));\r\n  gap: var(--spacing-lg);\r\n}\r\n\r\n.search-placeholder,\r\n.empty-results {\r\n  text-align: center;\r\n  padding: var(--spacing-2xl);\r\n  color: var(--color-text-muted);\r\n}\r\n\r\n.search-placeholder :deep(.el-icon),\r\n.empty-results :deep(.el-icon) {\r\n  margin-bottom: var(--spacing-lg);\r\n  opacity: 0.5;\r\n}\r\n\r\n.demo-info {\r\n  margin-top: var(--spacing-2xl);\r\n  background: rgba(239, 68,